import React from 'react'
import { NavLink } from 'react-router-dom'
import { FaShopify } from 'react-icons/fa'
import { RiLogoutBoxLine } from 'react-icons/ri';
import { FaCartArrowDown } from 'react-icons/fa'
import { useAuth } from '../../context/auth'
import { useCart } from '../../context/cart';
import { Badge } from 'antd';
import '../../index.css'

import '../../App.css'
import Searchinput from '../Form/Searchinput';
const Header = () => {
  const [cart] = useCart()
  const [auth, setAuth] = useAuth()
  const handleLogout = () => {
    setAuth({
      ...auth, user: null, token: ''
    })
    console.log(auth.user.role)
    localStorage.removeItem('auth')
    alert("Logout successfully")
  }
  return (
    <>
      <nav className="navbar navbar-expand-lg bg-body-tertiary">
        <div className="container-fluid">
          <NavLink to='/' className="navbar-brand" href="#"> <FaShopify /> BRAND</NavLink>
          <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
            <span className="navbar-toggler-icon" />
          </button>
          <div className="collapse navbar-collapse" id="navbarSupportedContent">
            <ul className="navbar-nav ms-auto mb-2 mb-lg-0">
              <Searchinput />
              <li className="nav-item ">
                <NavLink to='/' className="nav-link active" aria-current="page" href="#">Home</NavLink>
              </li>
              {/* <li className="nav-item ">
                {
                  <ul>
                    <NavLink to='/' className="nav-link active" aria-current="page" href="#">Category

                    </NavLink>
                  </ul>
                }

              </li> */}
              {
                !auth.user ? (<><li className="nav-item">
                  <NavLink to='/register' className="nav-link active" aria-current="page" href="#">Register</NavLink>
                </li>
                  <li className="nav-item">
                    <NavLink to='/login' className="nav-link active" aria-current="page" href="#">Login</NavLink>
                  </li>
                </>) : (<>
                  {/* <div class="collapse navbar-collapse" id="navbarNavDarkDropdown"> */}
                  <ul class="navbar-nav">
                    <li class="nav-item dropdown">
                      <button class="btn  dropdown-toggle" data-bs-toggle="dropdown" aria-expanded="false">
                        <NavLink>
                          {auth.user.name}
                        </NavLink>
                      </button>
                      <ul class="dropdown-menu dropdown-menu-dark">

                        <li><NavLink to={`/dashboard/${auth.user.role === 1 ? "admin" : "user"}`} class="dropdown-item" >Dashboard</NavLink></li>
                        {/* <li><NavLink to="/dashboard" class="dropdown-item" >Dashboard</NavLink></li> */}
                        <li className="dropdown-item">
                          <NavLink onClick={handleLogout} to='/login' className="nav-link active" aria-current="page" >Logout <RiLogoutBoxLine size={30} /> </NavLink>
                        </li>
                      </ul>
                    </li>
                  </ul>
                  {/* </div> */}
                </>)
              }
              <li className="nav-item">
                <Badge count={cart.length}>
                  <NavLink to='/cart' className="nav-link active" aria-current="page" href="#"><FaCartArrowDown />{cart.length}</NavLink>
                </Badge>
              </li>
            </ul>
          </div>
        </div>
      </nav>
    </>
  )
}
export default Header
