import React from 'react'
import Layout from '../../components/layouts/Layout'
import AdminMenu from '../../components/layouts/AdminMenu'
import { useEffect, useState } from 'react'
import '../../index.css'
import axios from 'axios'
import CatagoryForm from '../../components/Form/CatagoryForm'
import { } from 'antd'
import Modal from 'antd/es/modal/Modal'
const CreateCatagory = () => {
    const [visible, setVisible] = useState(false)
    const [categories, setCategories] = useState([])
    const [name, setName] = useState("")
    const [selected, setSelected] = useState(null)
    const [updatedName, setUpdatedName] = useState("")
    // *Handle form
    const handleSubmit = async (e) => {
        e.preventDefault()
        try {
            const { data } = await axios.post('/api/v1/catagory/create-catagory', { name })
            if (data) {
                alert("Updated sucessfully")
            }
            else {
                alert(data)
            }
        } catch (error) {
            console.log(error)
            alert("Something went wrong")
        }
    }
    // !Get all catagory
    const getAllCategory = async (req, res) => {
        try {
            const { data } = await axios.get('/api/v1/catagory/get-category')
            if (data.success) {
                setCategories(data.category);
                getAllCategory()
            }
        } catch (error) {
            console.log(error)
            alert("something went wrong")
        }
    }
    useEffect(() => {
        getAllCategory();
    }, [])
    // Update catagory
    const handleUpdate = async (e) => {
        e.preventDefault()
        try {
            const { data } = await axios.put(`/api/v1/catagory/update-category/${selected._id}`, { name: updatedName })
            if (data.success) {
                alert(`${updatedName} Successfully`)
                setSelected(null)
                setUpdatedName("")
                setVisible(false)
                getAllCategory();
            }
            else {
                alert("Some thing error")
            }
        } catch (error) {
            console.log(error)
            alert(error);
        }
    }
    // Delete catagory
    const handleDelete = async (pId) => {
        try {
            const { data } = await axios.delete(`/api/v1/catagory/delete-catagory/${pId}`)
            if (data.success) {
                getAllCategory();
            }
            else {
                alert("Some thing error")
            }
        } catch (error) {
            console.log(error)
            alert(error);
        }
    }
    return (
        <Layout title="Dashboard-Create Catagory">
            <div className='container-fluid m-3 p-3'>
                <div className='row'>
                    <div className=' col-md-3'>
                        <AdminMenu />
                    </div>
                    <div className=' col-md-9'>
                        <h1>CreateCatagory</h1>
                        <div className=' p-3 w-50'>
                            <CatagoryForm handleSubmit={handleSubmit} value={name}
                                setValue={setName}
                            />
                        </div>
                        <div>
                            <table className="table">
                                <thead>
                                    <tr>
                                        <th scope="col">Name</th>
                                        <th scope="col">Action</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {categories.map((c) => (
                                        <>
                                            <tr>
                                                <td key={c._id}>{c.name}</td>
                                                <td><button type="" className='btn btn-primary ms-2' onClick={() => {
                                                    setVisible(true); setUpdatedName(c.name)
                                                    setSelected(c)
                                                }}>Edit</button>
                                                    <button type="" className='btn btn-danger ms-2' onClick={() => { handleDelete(c._id)}}>Delete</button></td>
                                            </tr>
                                        </>
                                    ))}
                                </tbody>
                            </table>
                            <Modal onCancel={() => setVisible(false)} footer={null} visible={visible}>
                                <CatagoryForm value={updatedName} setValue={setUpdatedName} handleSubmit={handleUpdate} />
                            </Modal>
                        </div>
                    </div>
                </div>
            </div>
        </Layout>
    )
}
export default CreateCatagory