import React from 'react'
import { useState, useEffect } from 'react'

import Layout from '../components/layouts/Layout'
import { useAuth } from '../context/auth'
import axios from 'axios'
import { Checkbox, Radio } from 'antd'
import { json, useNavigate } from 'react-router-dom'
import { Prices } from '../components/Prices'
import Product from './Admin/Product'
import { Link } from 'react-router-dom'
import { useCart } from '../context/cart'
const HomePage = () => {
  const navigate = useNavigate()
  const [product, setProduct] = useState([])
  const [category, setCatagory] = useState([])
  const [checked, setchecked] = useState([])
  const [radio, setradio] = useState([])
  const [total, setTotal] = useState(0);
  const [page, setPage] = useState(1);
  const [loading, setLoading] = useState();
  const [cart, setCart] = useCart([])

  // Get total count:

  const getTotal = async () => {
    try {
      const { data } = await axios.get('/api/v1/product/product-count')
      setTotal(data.total)
    } catch (error) {
      console.log(error)
    }
  }

  const getAllCategory = async (req, res) => {
    try {
      const { data } = await axios.get('/api/v1/catagory/get-category')
      if (data.success) {
        setCatagory(data.category);
        getAllCategory()
      }
    } catch (error) {
      console.log(error)
      alert("something went wrong")
    }
  }
  useEffect(() => {
    getAllCategory();
    getTotal()
  }, [])
  // Get all product

  const getAllProduct = async () => {
    try {
      setLoading(true)

      const { data } = await axios.get('/api/v1/product/get-product')
      // const { data } = await axios.get(`/api/v1/product/product-list/${page}`)

      setLoading(false);
      setProduct(data.products);
    } catch (error) {
      setLoading(false)
      console.log(error)
    }
  }

  // !Filter by category 
  const handleFilter = (value, id) => {
    let all = [...checked]
    if (value) {
      all.push(id)

    }
    else {
      all = all.filter(c => c !== id)
    }
    setchecked(all);
  }

  useEffect(() => {
    if (!checked.length || !radio.length) getAllProduct();
    getAllProduct()
  }, [checked.length, radio.length])
  useState(() => {
    if (checked.length || radio.length) {
      filterProduct()
    }
  }, [checked, radio])

  // ?GEt fillter product
  const filterProduct = async () => {
    try {
      const { data } = await axios.post(`/api/v1/product/product-filters`, { checked, radio })
      setProduct(data.products)
    } catch (error) {
      console.log(error)
    }
  }

  useEffect(() => {
    if (page === 1) return;
    Laodmore()
  }, [page])
  const Laodmore = async () => {
    try {
      setLoading(true)
      const { data } = await axios.get(`/api/v1/product/product-list/${page}`)
      setLoading(false);
      setProduct([...product, data.products])
    } catch (error) {
      console.log(error);
      setLoading(false)
    }
  }
  return (<Layout>
    <div className='row mt-4'>
      <div className='col-md-3'>
        <h1 className=' text-center'>Filter By Catagory</h1>
        <div className='d-flex flex-col'>
          {
            category.map(c => (
              <Checkbox key={c._id} onChange={(e) => handleFilter(e.target.checked, c._id)}>
                {c.name}
              </Checkbox>
            ))
          }
        </div>
        {/* // ?Prices filter */}
        <h1 className=' text-center'>Filter By Prices</h1>
        <div className='d-flex flex-column'>
          <Radio.Group onChange={e => setradio(e.target.value)}>
            {Prices.map(p => (
              <div key={p._id}>
                <Radio value={p.array}>{p.name}</Radio>
              </div>
            ))}
          </Radio.Group>
        </div>
        <div className='d-flex flex-column'>
          <button className='btn btn-danger' onClick={() => window.location.reload()}>RESET</button>
        </div>
      </div>
      <div className='col-md-9'>
        {JSON.stringify(checked, null, 4)}
        {JSON.stringify(radio, null, 4)}
        <h4 className='text-center'>All products</h4>
        {/* <Product/> */}
        <div className='d-flex flex-wrap'>
          {product.map((p) => (
            // <Link to={`/deshboard/admin/products/ ${p.slug}`}
            //   className='product-link'
            // >
            <div className="card m-2" style={{ width: "18rem" }}>
              <img
                src={`/api/v1/product/product-photo/${p._id}`}
                className="card-img-top"
                alt={p.name}
              />
              <div className="card-body">
                <h5 className="card-title">{p.name}</h5>
                <p className="card-text">{p.description}</p>
                <p className="card-text"> $ {p.price}</p>
                <button type="" className='btn btn-primary m-3'
                  onClick={() => navigate(`/product/${p.slug}`)}
                >More details</button>
                <button type="" className='btn btn-primary'
                  onClick={() => {
                    setCart([...cart, p])
                    localStorage.setItem('cart', JSON.stringify([...cart, p]))
                    alert("Item are added on cart sucessfully")
                  }
                  }
                >Add to cart</button>
              </div>
            </div>
            // </Link>
          ))}
        </div>
        <div className=' m-2 p-3 '>
          {product.length < total && (
            <button type="" className='btn btn-warning' onClick={(e) => {
              e.preventDefault()
              setPage(page + 1);
            }}>
              {loading ? "Loading....." : "Laodmore.."}
            </button>
          )}
        </div>
      </div>
    </div>
  </Layout>
  )
}
export default HomePage


