import React, { useEffect, useState } from 'react';
import AdminMenu from '../../components/layouts/AdminMenu';
import Layout from '../../components/layouts/Layout';
import moment from 'moment';
import { useAuth } from '../../context/auth';
import axios from 'axios';
import { Select } from 'antd';
import { Option } from 'antd/es/mentions';

const AdminOrder = () => {
    const [status, setStatus] = useState(["Not processed", "Packeging", "Shipted", "Delivered"])
    const [order, setOrder] = useState([]);
    const [auth] = useAuth(); // Removed unused setAuth and other unused variables
    const [changeStatus, setChangeStatus] = useState()

    const getOrder = async () => {
        try {
            const { data } = await axios.get('/api/v1/auth/all-orders');
            setOrder(data);
        } catch (error) {
            console.log(error);
        }
    };
    const handleChange = async (orderId, value) => {
        try {
            const { data } = await axios.put(`api/v1/auth/order-status/${orderId}`, { status: value })
            getOrder()
        } catch (error) {
            console.log(error)
        }
    }
    useEffect(() => {
        if (auth.token) {
            getOrder();
        }
    }, [auth.token]);

    return (
        <>
            <Layout>
                <div className='row'>
                    <div className='col-md-3'>
                        <AdminMenu />
                    </div>
                    <div className='col-md-9'>
                        <h2 className='text-center'>All Orders</h2>
                        {order.map((o, i) => (
                            <div className='border shadow' key={o._id}>
                                <table className='table'>
                                    <thead>
                                        <tr>
                                            <th scope='col'>#</th>
                                            <th scope='col'>Status</th>
                                            <th scope='col'>Buyer</th>
                                            <th scope='col'>Order Date</th>
                                            <th scope='col'>Payment</th>
                                            <th scope='col'>Quantity</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        <tr>
                                            <th>{i + 1}</th>
                                            <th>
                                                <Select bordered={false} onChange={(value, orderId) => handleChange(o._id, value)} defaultValue={order.status} >
                                                    {
                                                        status.map((s, i) => (
                                                            <Option key={i} value={status}>
                                                                {s}
                                                            </Option>
                                                        ))
                                                    }
                                                </Select>
                                            </th>
                                            <th>{o.buyer.name}</th> {/* Added optional chaining */}
                                            <th>{moment(o.createdAt).fromNow()}</th> {/* Corrected property name */}
                                            <th>{o.payment.success ? 'Success' : 'Failed'}</th> {/* Added optional chaining */}
                                            <th>{o.products.length}</th>
                                        </tr>
                                    </tbody>
                                </table>
                                <div className='container'>
                                    {o.products.map((p) => (
                                        <div className='row card flex-row p-3' key={p._id}>
                                            <div className='col-md-4 card'>
                                                <img
                                                    src={`/api/v1/product/product-photo/${p._id}`}
                                                    className='card-img-top'
                                                    alt={p.name}
                                                    width={'100px'}
                                                    height={'100px'}
                                                />
                                            </div>
                                            <div className='col-md-8'>
                                                <h4>{p.name}</h4>
                                                <p>{p.description.substring(0, 30)}</p>
                                                <h2>Price: {p.price}</h2>
                                            </div>
                                        </div>
                                    ))}
                                </div>
                            </div>
                        ))}
                    </div>
                </div>
            </Layout>
        </>
    );
};

export default AdminOrder;