import React from 'react'
import { useState, useEffect } from 'react'
import Layout from '../../components/layouts/Layout'
import UserMenu from '../../components/layouts/UserMenu'
import { useAuth } from '../../context/auth'
import '../../styles/AuthStyles.css'
import '../../index.css'
import axios from 'axios'
const Profile = () => {
    // Context
    const [auth, setAuth] = useAuth()
    const [name, setName] = useState('')
    const [email, setemail] = useState('')
    const [password, setpassword] = useState('')
    const [phone, setphone] = useState('')
    const [address, setAddresss] = useState('')


    // get user data
    useEffect(() => {

        const { email, name, password, phone, address } = auth.user
        setName(name)
        setemail(email)
        setAddresss(address)
        setphone(phone)
        setpassword(password)

    }, [auth.user])
    const handleSubmit = async (e) => {
        e.preventDefault()
        // **console.log(name, email, password, phone, address)
        //? Toast.success('Register sucessfully')

        try {
            // const res =await axios.post(`${process.env.REACT_APP_API}/api/v1/auth/register`,{name,email,password,phone,address});

            const { data } = await axios.put('/api/v1/auth/profile', { name, email, password, phone, address })
            if (data.error) {
                alert("Something error")

            }
            else {
                setAuth({ ...auth, user: data.updatedUser })
                let ls = localStorage.getItem("auth")
                ls = JSON.parse(ls)
                ls.user = data.updatedUser
                localStorage.setItem('auth', JSON.stringify(ls))
                alert("Profile update sucessfull")

            }
        } catch (error) {
            console.log(error)
        }
    }
    return (
        <Layout>
            <div className=' container-fluid m-3 p-3'>
                <div className=' row'>
                    <div className=' col-md-3'>
                        <UserMenu />
                    </div>
                    <div className=' col-md-9'>
                        <h1>User Profile</h1>
                        <form className=' mt-4 items-center'
                            onSubmit={handleSubmit}
                        >
                            <div className="mb-3">
                                <input type="text"
                                    value={name}
                                    onChange={(e) => setName(e.target.value)}

                                    className="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder='Name'


                                />
                            </div>
                            <div className="mb-3">

                                <input type="text"
                                    value={email}
                                    onChange={(e) => setemail(e.target.value)}
                                    className="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder='Enter your email'

                                    disabled
                                />
                            </div>
                            <div className="mb-3">
                                <input type="password"
                                    value={password}
                                    onChange={(e) => setpassword(e.target.value)}
                                    className="form-control" id="exampleInputPassword1" placeholder='password'

                                    disabled
                                />
                            </div>
                            <div className="mb-3">
                                <input type="text"
                                    value={phone}
                                    onChange={(e) => setphone(e.target.value)}
                                    className="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder='Phone no'

                                />
                            </div>
                            <div className="mb-3">
                                <input type="text"
                                    value={address}
                                    onChange={(e) => setAddresss(e.target.value)}
                                    className="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder='Address'

                                />
                            </div>
                            <button type="submit" className="btn btn-primary">Update Profile</button>
                        </form>
                    </div>
                </div>
            </div>
        </Layout>
    )
}

export default Profile