import React, { useEffect, useState } from 'react'
import Layout from '../components/layouts/Layout'
import { useCart } from '../context/cart'
import { useAuth } from '../context/auth'
import { useNavigate } from 'react-router-dom'
import DropIn from 'braintree-web-drop-in-react'
import axios from 'axios'
const cartPage = () => {
    const [auth] = useAuth()
    const [cart, setCart] = useCart()
    const navigate = useNavigate()
    const [clientToken, setClientToken] = useState("")
    const [instance, setInstance] = useState("")
    const [loading, seetLoading] = useState(false)


    const totalPrice = () => {
        try {
            let total = 0
            cart.map((item) => { total = total + item.price })
            return total.toLocaleString("en-US", {
                style: "currency",
                currency: "INR"

            })
        } catch (error) {
            console.log(error)
        }
    }
    const removeCartItem = (pid) => {
        try {
            let myCart = [...cart]
            let index = myCart.findIndex(item => item._id == pid)
            myCart.splice(index, 1)
            setCart(myCart)
            localStorage.setItem('cart', JSON.stringify(myCart))
        } catch (error) {
            console.log(error)
        }
    }

    // Get payment getway
    // const getToken = async (req, res) => {
    //     try {
    //         //const { data } = await axios.get('/api/v1/product/braintree/token')
    //         setClientToken(data.clientToken)
    //     } catch (error) {
    //         console.log(error)
    //     }
    // }
    // useEffect(() => { getToken(); }, [auth.token])
    // const handlePayment = async () => {
    //     try {
    //         seetLoading(true)
    //         const { nonce } = await instance.requestPaymentMethod()
    //         // const { data } = await axios.post('api/v1/product/braintree/payment', {
    //         //     nonce, cart
    //         // })
    //         seetLoading(false)
    //         navigate('/dashboard/user/order')
    //         alert('order')
    //     } catch (error) {
    //         console.log(error)
    //         seetLoading(false)

    //     }
    // }
    return (
        <Layout>
            <div className='conrtainer'>
                <div className='row'>
                    <div className='col-md-12'>
                        <h1 className='text-center bg-light p-3'>
                            {`Hello ${auth.token && auth.user.name}`}
                        </h1>
                        <h4 className='text-center'>
                            {cart.length > 1 ? `You have ${cart.length} No of product ${auth.token ? "" : "Please login checkout"}` : "Your cart is empty"}
                        </h4>
                    </div>
                </div>
                <div className='row'>
                    <div className=' col-md-6'>
                        <div className=' row'>
                            {
                                cart.map((p) => (
                                    <div className='row card flex-row p-3'>
                                        <div className='col-md-4 card'>
                                            <img
                                                src={`/api/v1/product/product-photo/${p._id}`}
                                                className="card-img-top"
                                                alt={p.name}
                                                width={"100px"}
                                                height={"100px"}
                                            />
                                        </div>
                                        <div className='col-md-8'>
                                            <h4>{p.name}</h4>
                                            <p>{p.description.substring(0, 30)}</p>
                                            <h2>Price:{p.price}</h2>
                                            <button type="" className='btn btn-danger' onClick={() => removeCartItem(p._id)}>REMOVE</button>
                                            {/* <button type="" className='btn btn-outline-warning ml-3' onClick={() => navigate('/dashboard/user/codorder')}>Buy Now</button> */}
                                        </div>
                                    </div>
                                ))
                            }
                        </div>
                    </div>
                    <div className=' col-md-6 text-center'>
                        <h2>Cart Summary</h2>
                        <p>Total | CheckOut | Payment</p>
                        <hr />
                        <h1>Total :{totalPrice()} </h1>
                        {
                            auth.user.address ? (
                                <>
                                    <div className='mb-3'>
                                        <h4>Current address</h4>
                                        <h5>{auth.user.address}</h5>
                                        <button type="" className='btn btn-outline-danger' onClick={() => navigate('/dashboard/user/profile')}>UPDATE ADDRESS</button>
                                    </div>
                                </>
                            ) : (
                                <div className='mb-3'>
                                    {
                                        auth.user.token ? (
                                            <button type="" className='btn btn-warning' onClick={() => navigate('/dashboard/user/profile')}>UPDATE</button>
                                        ) : (
                                            <button type="" className='btn btn-warning' onClick={() => navigate('/login'
                                                ,
                                                { state: '/cart' }
                                            )}>LOgin</button>
                                        )
                                    }
                                </div>
                            )
                        }
                        <div className='mt-2'>
                            {
                                !clientToken || !cart.length ? ("") : (
                                    <>
                                        {/* <DropIn
                                            options={{
                                                authorization: clientToken,
                                                paypal: {
                                                    flow: 'vault'
                                                }
                                            }}
                                            onInstance={(instanse) => setInstance(instanse)}
                                        /> */}
                                        <button type="" className='btn btn-warning'
                                            //onClick={handlePayment}
                                            // disabled={ !auth.user.address}
                                        >{loading ? "Processing...." : "Make Pament"}</button>
                                    </>
                                )
                            }
                        </div>
                    </div>
                </div>
            </div>
        </Layout>
    )
}

export default cartPage