import React from 'react'
import Header from './header'
import Footer from './Footer'

const Layout = ({children}) => {
  return (
  <div>
    <title>
      {children}
    </title>
    <Header/>
    <main style={{minHeight:'80vh'}}>
      
      {children}
    </main>
    <Footer/>
  </div>
  )
}
export default Layout