import React from 'react'
import Layout from '../components/layouts/Layout';
import { useSearch } from '../context/search';
const Search = () => {
    const [values, setValues] = useSearch()
    return (
        <Layout>
            <div className='container'>
                <div className='text-center'>
                    <h1>Search Result</h1>
                    <h6>{values.results.length < 1 ? 'Product Not Found' : `Found${values.results.length}`} </h6>
                    <div className='d-flex flex-wrap mt-4 w-10 h-2'>
                        {values.results.map((p) => (
                            <div className='card m-2' style={{width:"18rem"}}>
                                <img src={`api/v1/product/product-photo/${p._id}`} alt={p.name} />
                                <div className='card-body'>
                                    <h5>{p.name}</h5>
                                    <h5 className='card-title'>{p.name}</h5>
                                    <p className='card-text'>
                                        {p.description}
                                    </p>
                                    <p>${p.price}</p>
                                    <button type="" className='btn btn-primary ml-3'>More Details</button>
                                    <button type="" className='btn btn-secondary ml-4'>Add to Cart</button>
                                </div>
                            </div>
                        )
                        )}
                    </div>
                </div>

            </div>
        </Layout>


    )
}

export default Search