import React, { useState } from 'react'
import Layout from '../../components/layouts/Layout'
import { useNavigate } from 'react-router-dom'
// import { Toast } from 'react-toastify/dist/components';
// import 'react-toastify/dist/ReactToastify.css';
import '../../styles/AuthStyles.css'
import axios from 'axios'
const Register = () => {
  const [name, setName] = useState('')
  const [email, setemail] = useState('')
  const [password, setpassword] = useState('')
  const [phone, setphone] = useState('')
  const [address, setAddresss] = useState('')
  const [answer, setAnswer] = useState('')
  const navigate = useNavigate()
  // ? Form function
  const handleSubmit = async (e) => {
    e.preventDefault()
    // **console.log(name, email, password, phone, address)
    //? Toast.success('Register sucessfully')
    try {
      // const res =await axios.post(`${process.env.REACT_APP_API}/api/v1/auth/register`,{name,email,password,phone,address});
      const res = await axios.post('/api/v1/auth/register',{name,email,password,phone,address,answer})
      if (res.data.success) {
        navigate("/login")
        alert("You can login now")
      }
      else {
        alert(res.data.message)
      }
    } catch (error) {
      console.log(error)
    }
  }
  return (
    <Layout title='register - Ecommer App'>
      <div className='form-container'>
        <h1>Register page</h1>
        <form className=' mt-4 items-center'
          onSubmit={handleSubmit}
        >
          <div className="mb-3">
            <input type="text"
              value={name}
              onChange={(e) => setName(e.target.value)}

              className="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder='Name'
              required

            />
          </div>
          <div className="mb-3">

            <input type="text"
              value={email}
              onChange={(e) => setemail(e.target.value)}
              className="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder='Enter your email'
              required
            />
          </div>
          <div className="mb-3">
            <input type="password"
              value={password}
              onChange={(e) => setpassword(e.target.value)}
              className="form-control" id="exampleInputPassword1" placeholder='password'
              required
            />
          </div>
          <div className="mb-3">
            <input type="text"
              value={phone}
              onChange={(e) => setphone(e.target.value)}
              className="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder='Phone no'
              required
            />
          </div>
          <div className="mb-3">
            <input type="text"
              value={address}
              onChange={(e) => setAddresss(e.target.value)}
              className="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder='Address'
              required
            />
          </div>
          <div className="mb-3">
            <input type="text"
              value={answer}
              onChange={(e) => setAnswer(e.target.value)}
              className="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder='Your first school name'
              required
            />
          </div>
          <button type="submit" className="btn btn-primary">Submit</button>
        </form>
      </div>
    </Layout>
  )
}
export default Register