// import React from 'react'
import Layout from '../../components/layouts/Layout'
import React, { useState } from 'react'
import axios from 'axios'
// import Layout from '../../components/layouts/Layout'
import { useNavigate, useLocation } from 'react-router-dom'
import { useAuth } from '../../context/auth'

const ForgotPassword = () => {
    const [email, setemail] = useState('')
    const [newpassword, setNewPassword] = useState('')
    const [answer, setAnswer] = useState('')
    const navigate = useNavigate()
    const location = useLocation()
    const [auth, setAuth] = useAuth()
    const handleSubmit = async (e) => {
        e.preventDefault()
        // **console.log(name, email, password, phone, address)
        //? Toast.success('Register sucessfully')

        try {
            // const res =await axios.post(`${process.env.REACT_APP_API}/api/v1/auth/register`,{name,email,password,phone,address});
            const res = await axios.post('/api/v1/auth/forgot-password', {
                email,
                // password 
                newpassword, answer
            })
            if (res && res.data.success) {
                // setAuth({
                //     ...auth,
                //     user: res.data.user,
                //     token: res.data.token,
                // });
                // localStorage.setItem("auth", JSON.stringify(res.data))
                // // navigate("/")
                navigate("/login")

                ////navigate(location.state || '/')

                alert("Welcome to our website")
            }
            else {
                alert(res.data.message)

            }
        } catch (error) {
            console.log(error)
            alert("Enter right username and password ")
        }
    }

    return (
        <Layout>
            <div className='form-container'>
                {/* <h1>Login page</h1> */}
                <h1>Forgot passsword</h1>
                <form className=' mt-4 items-center'
                    onSubmit={handleSubmit}
                >
                    <div className="mb-3">
                        <input type="text"
                            value={email}
                            onChange={(e) => setemail(e.target.value)}
                            className="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder='Enter your email'
                            required
                        />
                    </div>
                    <div className="mb-3">
                        <input type="password"
                            value={newpassword}
                            onChange={(e) => setNewPassword(e.target.value)}
                            className="form-control" id="exampleInputPassword1" placeholder='password'
                            required
                        />
                    </div>

                    <div className="mb-3">
                        <input type="text"
                            value={answer}
                            onChange={(e) => setAnswer(e.target.value)}
                            className="form-control" id="exampleInputPassword1" placeholder='Enter your secret answer'
                            required
                        />
                    </div>

                    <button type="submit" className="btn btn-primary">Rest</button>
                </form>
            </div>

        </Layout>
    )
}

export default ForgotPassword