import React from 'react'
import { NavLink } from 'react-router-dom'
const UserMenu = () => {
    return (
        <>
            <div className='   .text-center'>
                <h1 class="list-group-item active" aria-current="true">Dash Board</h1>
                <ul class="list-group">
                    <NavLink to='/dashboard/user/profile' className=" no-underline">Profile</NavLink>
                    <NavLink to='/dashboard/user/order'>Orders</NavLink>
                    {/* <NavLink to='/dashboard/admin/user' >User</NavLink> */}
                    {/* <NavLink to='/dashboard/admin/user' > </NavLink> */}
                </ul>
            </div>
        </>
    )
}

export default UserMenu