
import React, { useState, useEffect } from 'react'
import Layout from '../../components/layouts/Layout'
import AdminMenu from '../../components/layouts/AdminMenu'
import axios from 'axios'
import { Select } from 'antd'
import { useNavigate,useParams } from 'react-router-dom'
const { Option } = Select
const UpdateProduct = () => {
    const navigate = useNavigate()
    const params=useParams();
    const [categories, setCategories] = useState([])
    const [photo, setPhoto] = useState("")
    const [name, setName] = useState("")
    const [description, setDescription] = useState("")
    const [catagory, setCatagory] = useState("")
    const [price, setPrice] = useState("")
    const [quantity, setQuantity] = useState("")
    const [shipping, setShipping] = useState("")

// *Get single product

const getSingleProduct=async()=>{
    try {
        const {data}=await axios.get(`/api/v1/product/get-product/${params.slug}`)
        setName(data.product.name)
    } catch (error) {
        console.log(error);
    }
};
useEffect(()=>{
getSingleProduct()
},[])
    // !Get all catagory
    const getAllCategory = async (req, res) => {
        try {
            const { data } = await axios.get('/api/v1/catagory/get-category')
            if (data.success) {
                setCategories(data.category);
                getAllCategory()
            }
        } catch (error) {
            console.log(error)
            alert("something went wrong")
        }
    }
    useEffect(() => {
        getAllCategory();
    }, [])

    // Create product function
    const handleCreate = async (e) => {
        e.preventDefault()
        try {
            const productData = new FormData()
            productData.append("name", name)
            productData.append("description", description)
            productData.append("price", price)
            productData.append("quantity", quantity)
            productData.append("photo", photo)
            productData.append("catagory", catagory)
            const { data } = axios.post('/api/v1/product/create-product', productData)
            // const { data } = axios.post('/api/v1/product/create-product')
            if (data) {
                alert("sucessfulle send data")
                // navigate('/deshboard/admin/products')
            }
            else {
                alert(e)
            }
        }
        catch (error) {
            
        }
    }
    return (
        <Layout>
            <div className='container-fluid m-3 p-3'>
                <div className='row'>
                    <div className=' col-md-3'>
                        <AdminMenu />
                    </div>
                    <div className=' col-md-9'>
                        <h1>Update Product</h1>
                        <div className=' m-1 w-75'>
                            <Select bordered={false}
                                placeholder="Select a catagory"
                                showSearch
                                className=' form-select'
                                onChange={(value) => {
                                    setCatagory(value)
                                }}
                            >
                                {categories.map((c) => (
                                    <Option key={c._id} value={c._id}>
                                        {c.name}
                                    </Option>
                                ))}
                            </Select>
                            <div className='mb-3'>
                                <label className=' btn btn-outline-secondary col-md-12'>
                                    {photo ? photo.name : "Upload Photo"}
                                    <input type="file" name="photo" accept="image/*" onChange={(e) => setPhoto(e.target.files[0])}
                                        hidden />
                                </label>
                            </div>
                            <div className='mb-3'>
                                {photo && (
                                    <div className='text-center'>
                                        <img src={URL.createObjectURL(photo)} alt="product_photo" height={'200px'} className='img img-responsive' />
                                    </div>
                                )}
                            </div>
                            <div className='mb-3'>
                                <input type="text"
                                    value={name}
                                    className='form-control'
                                    name="" placeholder='Write your name'
                                    onChange={(e) => setName(e.target.value)}
                                />
                            </div>
                            <div className="mb-3">
                                <textarea
                                    type="text"
                                    value={description}
                                    placeholder="write a description"
                                    className="form-control"
                                    onChange={(e) => setDescription(e.target.value)}
                                />
                            </div>
                            <div className="mb-3">
                                <input
                                    type="number"
                                    value={price}
                                    placeholder="write a Price"
                                    className="form-control"
                                    onChange={(e) => setPrice(e.target.value)}
                                />
                            </div>
                            <div className="mb-3">
                                <input
                                    type="number"
                                    value={quantity}
                                    placeholder="write a quantity"
                                    className="form-control"
                                    onChange={(e) => setQuantity(e.target.value)}
                                />
                            </div>
                            <div className="mb-3">
                                <Select
                                    bordered={false}
                                    placeholder="Select Shipping "
                                    size="large"
                                    showSearch
                                    className="form-select mb-3"
                                    onChange={(value) => {
                                        setShipping(value);
                                    }}
                                >
                                    <Option value="0">No</Option>
                                    <Option value="1">Yes</Option>
                                </Select>
                            </div>
                            <div className='mb-3'>
                                <button type="" className='btn btn-primary' onClick={handleCreate}>Update Product</button>

                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </Layout>
    )
}

export default UpdateProduct